import { Dialog, Form, FormItem, Button, Input} from 'element-ui'

export default {
    components: {
        'el-dialog': Dialog,
        'el-form': Form,
        'el-form-item': FormItem,
        'el-button': Button,
        'el-input': Input
    },
    props: ['show', 'data'],
    data() {
        return {
            dialogShow: false,
            formLabelWidth: '100px',
            title: '',
            form: {
                name: '',
                content: ''
            },
            model: {},
        }
    },
    methods: {
        reject() {
            this.$emit('reject', {content: this.form.content, model_id: this.model.id})
            this.dialogShow = false
        }
    },
    watch: {
        show () {
            this.dialogShow = this.show
        },
        dialogShow () {
            if (!this.dialogShow) {
                this.$emit('close')
                this.form = {
                    name: '',
                        content: ''
                };
            }
        },
        data() {
            this.model = this.data
        }
    }
}



