import { Dialog, Button, Col, Form, FormItem, Input } from 'element-ui'

export default {
    components: {
        'el-dialog': Dialog,
        'el-button': Button,
        'el-col': Col,
        'el-form': Form,
        'el-form-item': FormItem,
        'el-input': Input
    },
    props:['show', 'data'],
    data () {
        return {
            load: false,
            dialogShow: this.show,
            formLabelWidth: '40px',
            user: {
                wechat: {
                    avatar: ''
                }
            },
        }
    },
    methods: {
        history() {
            this.$router.push({name:'user_report', params: {user_id: this.user.id}});
        }
    },
    watch: {
        show () {
            this.dialogShow = this.show;
            this.user = this.data;
        },
        dialogShow () {
            if (!this.dialogShow) {
                this.$emit('close');
            }
        }
    }

}



