    import { Pagination } from 'element-ui'

    export default{
        components:{'el-pagination': Pagination},
//        name: 'paginate',
        props: ['total'],
        data () {
            return {
                paginate: {
                    total: this.total,
                    page_size: this.$limit,
                },
                page: 1,
            }
        },
        methods: {
            change (val) {
                this.page = val;
            }
        },
        watch: {
            page: {
                handler: function (val) {
                    this.$emit('change', val);
                },
                deep: true
            },
            total(val) {
                this.paginate.total = val;
            }
        }
    }



